import { Form, Modal, Input, Select, DatePicker, message } from "antd";
import useIntlMessage from "../../../../../hooks/useIntlMessage";
import { useCallback, useEffect, useState } from "react";
import { EMP_FORM_FIELDS } from "constants/ManageEmployeeConstants";
import { COMMON_MESSAGES, NEW_DATE_FORMAT } from "../../../../../constants/CommonConstants";
import { SELECT_LAYOUT } from "constants/ManageProjectsConstant";
import { CHILDREN } from "../../../../../constants/HeadCountReportConstant";
import employeeService from "services/EmployeeService";
import Utils from "../../../../../utils/index";
import { SELECT_HIGHT } from "../../../../../constants/ManageProjectsConstant";
import { DATE_FORMAT } from "constants/CommonConstants";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";

const RehireForm = ({ visible, onCancel, rehireEmployee, handleRefresh }) => {
  const [form] = Form.useForm();
  const getMessage = useIntlMessage();
  const dispatch = useDispatch();
  const accountList = useSelector(state => state.myTeams.accountsList);
  const [designationsList, setDesignationsList] = useState([]);
  const { Option } = Select;
  const formItemLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 14 },
  };
  useEffect(() => {
    if (!Utils.isEmpty(rehireEmployee?.country)) {
      filteredDesignations(rehireEmployee?.country);
    }
  }, []);

  const filteredDesignations = async country => {
    let filteredDesig = await employeeService.getDesignationCountryBased(country);
    setDesignationsList(filteredDesig);
  };

  const HandleSubmit = () => {
    form
      .validateFields()
      .then(values => {
        saveData(values);
      })
      .catch(info => {});
  };
  const saveData = async values => {
    const payload = {
      ...values,
      employeeId: rehireEmployee?.employeeId,
      hrmsId: rehireEmployee?.hrmsId,
      rehireDate: values.rehireDate ? moment(values.rehireDate).format(NEW_DATE_FORMAT).valueOf() : null,
    };
    try {
      dispatch(showLoader(true));
      const response = await employeeService.terminate(payload);
      if (response.statusCode === 200) {
        message.success(response?.details);
        onCancel();
        handleRefresh();
      }
    } catch (err) {
      console.log(err);
    } finally {
      dispatch(showLoader(false));
    }
  };
  function disabledDate(current) {
    const endDate = rehireEmployee.endDate ? moment(rehireEmployee.endDate) : null;
    return current && current < endDate;
  }
  const isEditMode = useCallback(() => {
    return rehireEmployee && true;
  }, [rehireEmployee]);

  return (
    <Modal
      width="25%"
      visible={visible}
      onCancel={onCancel}
      onOk={HandleSubmit}
      bodyStyle={{ overflowY: SELECT_HIGHT.text, maxHeight: SELECT_HIGHT.value }}
      okText={"Activate"}
      title={"Employee Activate Form"}
    >
      <Form form={form} layout={SELECT_LAYOUT.text} name={"Employee Rehire Form"} {...formItemLayout}>
        <Form.Item
          label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.EMPLOYEE_ID.label)}</div>}
          name={EMP_FORM_FIELDS.EMPLOYEE_ID.name}
          rules={[
            {
              message: getMessage(COMMON_MESSAGES.REQIRED_PREFIX, EMP_FORM_FIELDS.EMPLOYEE_ID.label),
            },
          ]}
        >
          {isEditMode() ? (
            <span className="profile-text-color">{rehireEmployee?.employeeId}</span>
          ) : (
            <Input placeholder={getMessage(EMP_FORM_FIELDS.EMPLOYEE_ID.label)} />
          )}
        </Form.Item>
        <Form.Item label={getMessage(EMP_FORM_FIELDS.HRMS_ID.label)} name={EMP_FORM_FIELDS.HRMS_ID.name}>
          {isEditMode() ? (
            <span className="profile-text-color">{rehireEmployee?.hrmsId}</span>
          ) : (
            <Input placeholder={getMessage(EMP_FORM_FIELDS.HRMS_ID.label)} />
          )}
        </Form.Item>
        <Form.Item
          label={getMessage(EMP_FORM_FIELDS.MOBILE_NO.label)}
          name={EMP_FORM_FIELDS.MOBILE_NO.name}
          rules={[{ pattern: /^[\d.+-]{0,15}$/, message: getMessage(EMP_FORM_FIELDS.MOBILE_NO.invalidMessage) }]}
        >
          <Input
            placeholder={getMessage(EMP_FORM_FIELDS.MOBILE_NO.label)}
            onKeyPress={Utils.blockInvalidChar}
            min={0}
          />
        </Form.Item>
        <Form.Item
          label={getMessage(EMP_FORM_FIELDS.DESIGNATION.label)}
          name={EMP_FORM_FIELDS.DESIGNATION.name}
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            placeholder={getMessage(EMP_FORM_FIELDS.DESIGNATION.label)}
            allowClear
            showArrow
            showSearch
            optionFilterProp={CHILDREN}
            id="manage-emp-designation"
          >
            {designationsList?.map(e => {
              return (
                <Option key={e.designationId} value={e.designation}>
                  {e.designation}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          name={EMP_FORM_FIELDS.PARENT_ACCOUNT_NAME.name}
          label={getMessage(EMP_FORM_FIELDS.PARENT_ACCOUNT_NAME.label)}
          required={true}
        >
          <Select
            placeholder={getMessage(EMP_FORM_FIELDS.PARENT_ACCOUNT_NAME.label)}
            showArrow
            showSearch
            optionFilterProp={CHILDREN}
            // disabled={isEditMode()}
            id="manage-emp-parentaccount"
          >
            {accountList?.map(e => {
              return (
                <Option key={e.accountId} value={e.accountName}>
                  {e.accountName}
                </Option>
              );
            })}
          </Select>
        </Form.Item>
        <Form.Item
          label={<div className="text-wrap">{getMessage(EMP_FORM_FIELDS.REHIRE_DATE.label)}</div>}
          name={EMP_FORM_FIELDS.REHIRE_DATE.name}
          rules={[
            {
              required: true,
              message: getMessage(COMMON_MESSAGES.REQIRED_PREFIX, EMP_FORM_FIELDS.REHIRE_DATE.label),
            },
          ]}
        >
          <DatePicker
            // disabled={isEditMode()}
            format={DATE_FORMAT}
            disabledDate={disabledDate}
            id="emp-date-of-joining"
            className="w-100"
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default RehireForm;
